}

body{
  font-family: 'Poppins', sans-serif;
  color: #152E3A;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.66;
}
h1, h2, h3, h4, h5, h6{
  font-family: 'Roboto', sans-serif;
  color: #0F1327;
  font-weight: bold;
}
.header-area {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 2;
}
/* hero area css */

.site-btn a.video-btn {
  width: 60px;
  height: 60px;
  text-align: center;
  padding: 0;
  background: #F31717;
}

.site-btn a.video-btn i {
  margin: 0;
}
.hero-area {
  position: relative;
  z-index: 1;
  padding-top: 330px;
  padding-bottom: 100px;
  background: #F4F7FC;
}

.Hero-area-thumbnail-bottom {
  position: absolute;
  right: 18%;
  bottom: 0;
}
 

.header-top-right-content ul {
  margin: 0;
  padding: 0;
  text-align: right;
  list-style: none;
}

.hero-inner-content h5 {
  font-size: 20px;
  color: #F31717;
  margin: 0;
  margin-bottom: 15px;
}

.hero-inner-content h1 {
  font-size: 75px;
  line-height: 1.1;
  margin-bottom: 15px;
  color: #0F1327;
}
 

.hero-inner-content .desc p {
  margin: 0;
  font-size: 20px;
}

.hero-inner-content .desc {
  margin-bottom: 20px;
}

.site-btn a {
  display: inline-block;
  line-height: 60px;
  background: #0F1327;
  padding: 0 45px;
  margin-right: 20px;
  border-radius: 50px;
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
}

.site-btn a i {
  margin-left: 15px;
}

/* menu bar css */ 

.header-menubar ul li {
    display: inline-block;
}
 

.header-menubar nav.navbar {
    margin: 0;
    padding: 0;
}

.header-menubar ul li a {
    padding: 0 26px !important;
    margin: 0 !important;
    color: #0F1327;
    font-weight: 600;
    font-size: 18px;
    line-height: 60px;
}
.header-menubar ul li.nav-item.contactus a {
  background: #F31717;
  color: #fff;
  border-radius: 50px;
  padding: 0 45px !important;
}

a {
  transition: .3s;
}

.header-menubar ul li.nav-item.contactus a:hover {
  background: #0F1327;
  color: #fff;
}
.site-btn a:hover {
  background: #F31717;
}
.header-top-area {
  background: #0F1327;
  color: #fff;
  padding: 15px 0;
  margin-bottom: 15px;
}

.header-top-area ul {
  margin: 0;
  padding: 0;
}

.header-top-area p {
  margin: 0;
}
 

.header-top-left-content p i {
  color: #F31717;
}

.header-top-left-content p a {
  color: #fff;
}

.header-top-right-content ul li a {
  color: #fff;
  margin-left: 25px;
}

.header-top-right-content ul li a:hover {
  color: #F31717;
}

a.navbar-brand {
  padding: 10px 15px;
  border: 3px solid #222;
  text-transform: uppercase;
  color: #222;
  font-weight: bold;
}

.header-top-right-content ul li {
  display: inline-block;
}

.header-top-right-content {
  list-style: none;
}

img{
  max-width: 100%;
  object-fit: cover;
}


/* about us css */
.about-us-area {
  padding: 100px 0;
  position: relative;
  z-index: 1;
}

.common-title .hero-inner-content h1 {
  font-size: 45px;
}

.common-title .hero-inner-content h5 {}

.common-title {}

.about-right-side-content {}

.common-title {}

.common-title .hero-inner-content .desc p, .common-title .hero-inner-content .desc1 p {
  font-size: 16px;
}

.hero-inner-content {}

.common-title .des1 p {
  font-size: 16px;
}

.common-title .des1 {
  margin-bottom: 40px;
}

.about-inner-content {
  position: relative;
  padding-left: 60px;
  z-index: 1;
}
 

.about-inner-content:before {content: "";position: absolute;left: 25px;top: -30px;width: 150px;height: 150px;background: #F31717;z-index: -1;}

.about-video {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

.about-videos {
  text-align: center;
  width: 100%;
}
.about-right-side-content .site-btn a {
  font-size: 16px;
  padding: 0 35px;
  background: #F31717;
}

.years-exp {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
  background: #fff;
  padding: 13px;
  box-shadow: 0px 0px 30px #ddd;
}
 

.about-videos a {
  width: 60px;
  height: 60px;
  background: #F31717;
  display: inline-block;
  color: #fff;
  border-radius: 50%;
  line-height: 61px;
}
 

.twenty-five {
  font-size: 33px;
  margin-right: 12px;
  color: #F31717;
  font-weight: bold;
}
 
.about-right-side-content {
  padding-left: 50px;
}


/* Service items css */
.service-section-area {
  background: #fbfbfb;
  padding: 100px 0;
}

.service-section-area .common-title {
    margin-bottom: 55px;
}

.single-service-solution {
    box-shadow: 0px 0px 10px #ddd;
    margin-bottom: 30px;
    position: relative;
    z-index: 1;
    border-radius: 5px;
    padding: 35px;
    padding-top: 100px;
    transition: .3s;
}
 

.single-service-solution .count {
    width: 30px;
    height: 30px;
    background: #F53C3C;
    border-radius: 50%;
    color: #fff;
    font-size: 14px;
    line-height: 30px;
    position: absolute;
    right: 11%;
    top: 5%;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}
 

.single-service-solution .icon i {
    font-size: 57px;
    color: #F31717;
}

.single-service-solution .icon {
    margin-bottom: 16px;
}
 

.single-service-solution .title p {
    margin: 0;
    font-size: 24px;
    font-weight: bold;
    color: #222;
    margin-bottom: 0;
}

.single-service-solution .title {
    margin-bottom: 20px;
}

.single-service-solution:hover {
    background: #F31717;
}

.single-service-solution:hover .icon i {
    color: #fff;
}

.single-service-solution:hover .title p {
    color: #fff;
}

.single-service-solution:hover .desc p {
    color: #fff;
}

.single-service-solution .readmore a {
  display: inline-block;
  width: 30px;
  height: 30px;
  background: #fff;
  box-shadow: 0px 0px 10px rgb(221 221 221 / 58%);
  border-radius: 50%;
  line-height: 33px;
  color: #222;
  transition: .3s;
}

.single-service-solution:hover .readmore a {
  background: #fff;
}

.single-service-solution:hover .count {
  visibility: visible;
  opacity: 1;
}

/* footer area css */

.footer-area {
  background: #0F1327;
  color: #fff;
}
 

.footer-top-outer-row {
  padding: 30px 0;
  border-bottom: 1px solid #222;
}

.footer-widget-area {
  padding: 80px 0;
}

.copytext {
  border-top: 1px solid #222;
  padding: 20px  0;
}

.copytext p {
  margin: 0;
}

.top-left-content p {
  margin: 0;
}

.top-right-content {
  text-align: right;
}

.top-right-content .site-btn a {
  background: #F31717;
  margin-right: 0;
}

.top-right-content .site-btn a:hover {
  background: #fff;
  color: #F31717;
}
 

.footer-widget p.footer-widget-title {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 30px;
}

.footer-widget ul li {
    display: block;
    margin-bottom: 10px;
}

.footer-widget ul li a {
    display: inline-block;
    color: #fff;
    text-decoration: none;
}

.footer-widget ul {
    margin: 0;
    padding: 0;
}
.footer-form form {
  position: relative;
}

.footer-form form input[type="email"] {
  width: 100%;
  border: 1px solid #ddd;
  background: transparent;
  padding: 10px;
}

.breadcum-area {
  padding-top: 190px;
  padding-bottom: 100px;
  background: #F4F7FC;
}

.footer-form form button {
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: none;
  background: #fff;
  color: #222;
}

.social-list  a {
  display: inline-block;
  color: #fff;
  margin-right: 15px;
}

.social-list {
  margin-top: 20px;
}

.social-list a:hover {color: red;}


/* why choose us */
 

.why-choos-us .container-fluid {
    overflow: hidden;
    padding-left: 16%;
}
 

.why-choos-us {
  position: relative;
  z-index: 1;
  padding: 100px 0;
  color: #fff;
}

.why-choos-us:before {
  width: 60%;
  height: 100%;
  content: "";
  background: #0F1327;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.why-choos-us .common-title .hero-inner-content h1 {
  color: #fff;
}

.why-choos-us .common-title {
  margin-bottom: 35px;
}

.why-box {
  margin-bottom: 30px;
}

.why-box  .desc {}

.why-box  .desc p {
  margin: 0;
}

.why-box .desc p.title {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 8px;
}

.why-box .icon {
  width: 40px;
  height: 40px;
  text-align: center;
  border-radius: 5px;
  background: #222;
  line-height: 42px;
  margin-right: 15px;
  transition: .3s;
}

.why-box:hover .icon {
  background: red;
  color: #fff;
}

.why-choose-us-thumbnail {
  position: relative;
  z-index: 1;
}

.why-choose-us-thumbnail img {
  border-radius: 5px;
}

.why-choose-us-video.d-flex.align-items-center {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.video-btn {
  width: 100%;
  text-align: center;
}

.video-btn a {
  width: 60px;
  height: 60px;
  position: relative;
  z-index: 1;
  display: inline-block;
  line-height: 62px;
  font-size: 25px;
  color: #F31717;
}

.video-btn a:before, .video-btn a:after {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  content: "";
  background: rgb(255 255 255 / 90%);
  z-index: -1;
  border-radius: 50%;
}

.video-btn a:after {
  background: #F31717;
  z-index: -2;
  width: 110%;
  height: 110%;
  top: -5%;
  left: -5%;
}

/* logo area css */

.logo-outer {
  height: 170px;
  margin: 0px 15px;
}
 

.logo-outer-area {
  background: #fff;
  margin-top: -100px;
  z-index: 5;
  padding: 10px;
  box-shadow: 0px 0px 10px #ddd;
}

.logo-area {
  z-index: 1;
  position: relative;
}

.why-choos-us {
  z-index: 1;
}

.logo-area {
  padding-bottom: 100px;
}

.slick-prev, .slick-next {
  background: red !important;
  padding: 10px !important;
  width: 60px !important;
  height: 60px !important;
  border-radius: 50%!important;
  left: -100px !important;
}

.slick-next {
  left: auto!important;
  right: -100px!important;
}

/* Case study css */

.case-study-single {}

.case-study {
    padding-bottom: 100px;
}

.case-study .common-title {
    margin-bottom: 35px;
}

.case-study-single {
    position: relative;
    z-index: 1;
    margin-bottom: 35px;
    overflow: hidden;
}

.case-study-single img {
    width: 100%;
    object-fit: cover;
}

.sigle-case-study-meta {
    position: absolute;
    left: -100px;
    bottom: 10px;
    background: #fff;
    width: auto;
    padding: 10px;
    transition: .3s;
    opacity: 0;
    visibility: hidden;
}

.sigle-case-study-meta  p {
    margin: 0;
}

.sigle-case-study-meta a {
    color: #222;
    font-size: 20px;
    text-decoration: none;
    font-weight: bold;
}

.case-study-single:hover .sigle-case-study-meta {
    visibility: visible;
    opacity: 1;
    left: 10px;
}

.sigle-case-study-meta .category p {
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
}

/* contact form css */

.google-map {
  padding: 100px 0;
  background: #fbfbfb;
}

.google-map-info-box {
  margin-bottom: 35px;
}
 
.contact-infobox p.heading {
  font-size: 24px;
  font-weight: bold;
  color: #222;
  margin-bottom: 8px;
}

.google-map-info-box p {
  margin: 0;
}

.google-map-info-box .desc {
  margin-bottom: 10px;
}

.form-group {}

.google-map-right-side-content .form-group {
    margin-bottom: 15px;
}

input, textarea {
    background: #fff;
    border: 1px solid #ddd;
    padding: 10px 20px;
}

button[type="submit"] {
  border: none;
  line-height: 40px;
  padding: 0 13px;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
  border-radius: 5px;
  background: #222;
  transition: .3s;
}

.form-group input, .form-group textarea {
    width: 100%;
}

.form-group textarea {
    height: 125px;
}

button[type="submit"]:hover {
    background: #F31717;
}
